<template>
  <div id="home">
    <el-container style="height: 500px;">
      <el-header style="left: 0px; margin: 0px; padding: 0px"><itemheader></itemheader></el-header>
      <el-container>
        <el-aside>
          <div class="allconent2">
            <itemsidebar></itemsidebar>

          </div>
          
        </el-aside>
        <el-main><div class="allconent">
          <transition name="move" mode="out-in">
            <router-view></router-view>
          </transition>
        </div></el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import itemheader from "./public/header.vue";
import itemsidebar from "./public/sidebar.vue";
export default {
  name: "home",
  components: {
    itemheader,
    itemsidebar
  },
  data() {
    const item = {
      date: "2016-05-02",
      name: "王小虎",
      address: "上海市普陀区金沙江路 1518 弄"
    };
    return {
      tableData: Array(20).fill(item)
    };
  }
};
</script>

<style scoped>
body {
  margin: 0;
  padding: 0;
}
#app {
  color: black;
}
.allconent {
  background: none repeat scroll 0 0;
  position: absolute;
  left: 220px;
  right: 0;
  top: 60px;
  bottom: 0;
  width: auto;
  padding: 20px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  overflow-y: scroll;
}
.allconent2 {
  background: none repeat scroll 0 0;
  position: absolute;
  left: 0;
  top: 60px;
  bottom: 0;
  width: 200px;
  padding: 20px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  overflow-y: scroll;
  overflow-x: hidden
}
.billtable {
  margin-top: 20px;
}
</style>


