        <el-input v-model="formInline.user" placeholder="产品名称"></el-input>
      </el-form-item>
      <el-form-item>
        <el-input-number v-model="formInline.day" :min="1" :max="10" label="描述文字"></el-input-number>天
      </el-form-item>
      <el-form-item>
        <el-input-number v-model="formInline.night" :min="0" :max="10" label="描述文字"></el-input-number>晚
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
    name: 'add-line-basic'
}
</script>

