<template>
  <div id="validate-form">
    <h2>增加产品</h2>
    <el-divider></el-divider>
    <el-form :model="p" :rules="rules1" ref="ruleForm1" :inline="true" class="demo-form-inline">
      <el-form-item label="产品名称" prop="name">
        <div class="block">
          <el-input placeholder="请输入内容" v-model="p.name" clearable></el-input>
        </div>
      </el-form-item>
      <el-form-item label="目的地" prop="des_sel">
        <el-select v-model="p.des_sel" placeholder="请选择目的地">
          <el-option v-for=" i in des" :key="i.id" :label="i.name" :value="i.id"></el-option>
        </el-select>
      </el-form-item>
      <br />
      <el-form-item label="成年人价格" prop="adult_price">
        <el-input placeholder="请输入内容" type="number" v-model="p.adult_price" clearable></el-input>
      </el-form-item>
      <el-form-item label="儿童价格" prop="child_price">
        <el-input placeholder="请输入内容" type="number" v-model="p.child_price" clearable></el-input>
      </el-form-item>
      <el-form-item label="行程天数" prop="days">
        <el-input placeholder="请输入内容" type="number" v-model="p.days" clearable></el-input>
      </el-form-item>
    </el-form>
    <el-form :label-position="left" :model="r" :rules="rules2" ref="ruleForm2">
      <h3>行程内容</h3>
      <el-form-item label="服务标准(此处限填：交通、住宿、用餐、门票、导服、小孩标准、费用不含、购物、保险等)" prop="standard">
        <el-input type="textarea" :rows="8" placeholder="请输入内容" v-model="r.standard"></el-input>
      </el-form-item>

      <el-form-item label="行程备注(此处限填：温馨提示、自费项目、自由活动等)" prop="remark">
        <el-input type="textarea" :rows="8" placeholder="请输入内容" v-model="r.remark"></el-input>
      </el-form-item>
      

      <el-form-item label="产品特色(提示：赠送、自由选择)" prop="chara">
        <el-input type="textarea" :rows="8" placeholder="请输入内容" v-model="r.chara"></el-input>
      </el-form-item>

      <el-form-item label="产品报价分析(此处限填：设计思路、主要卖点、市场定位、销售渠道、利润产生点、执行周期等)" prop="price">
        <el-input type="textarea" :rows="8" placeholder="请输入内容" v-model="r.price"></el-input>
      </el-form-item>
      
      <br />
      <br />
      <div style="text-align: center">
        <el-button type="primary" plain round @click="submit()">提交产品库</el-button>
        <el-button @click="reset()">重置</el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "AddProduct",
  data() {
    return {
      des: [],
      va: false,
      p: {
        name: "",
        des_sel: "",
        adult_price: "",
        child_price: "",
        days: ""
      },
      r: {
        standard: "",
        remark: "",
        chara: "",
        price: ""
      },
      rules1: {
        name: [{ required: true, message: "请输入产品名称", trigger: "blur" }],
        des_sel: [{ required: true, message: "请选择目的地", trigger: "change" }],
        adult_price: [
          { required: true, message: "请输入成年人价格", trigger: "blur" }
        ],
        child_price: [
          { required: true, message: "请输入儿童价格", trigger: "blur" }
        ],
        days: [{ required: true, message: "请输入行程天数", trigger: "blur" }]
      },
      rules2: {
        standard: [
          { required: true, message: "请输入产品名称", trigger: "blur" }
        ],
        remark: [{ required: true, message: "请输入产品名称", trigger: "blur" }],
        chara: [{ required: true, message: "请输入产品名称", trigger: "blur" }],
        price: [{ required: true, message: "请输入产品名称", trigger: "blur" }]
      }
    };
  },
  methods: {
    async getDestinations() {
      try {
        //const response = await fetch('https://jsonplaceholder.typicode.com/users')
        const response = await fetch("http://localhost:8081/destination");
        const data = await response.json();
        this.des = data;
        //this.employees = data
        //alert(JSON.stringify(data));
      } catch (error) {
        alert(error);
      }
    },
    async addProduct(p) {
      try {
        const response = await fetch("http://localhost:8081/add_product", {
          method: "POST",
          body: JSON.stringify(p),
          headers: { "Content-type": "application/json; charset=UTF-8" }
        });
        const data = await response.json();
        //this.employees = [...this.employees, data]
      } catch (error) {
        console.error(error);
      }
    },
    submit() {
      this.va = true;
      this.onsubmit("ruleForm1");
      this.onsubmit("ruleForm2");
        if (this.va) {
          this.submit_confirm()
        }
    },
    submit_confirm() {
        this.$prompt('请输入密码', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputType:'password',
          inputPattern: /.......*/,
          //inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
          inputErrorMessage: '密码长度至少6位'
        }).then(({ value }) => {
          this.post(value);
        });
    },
    post(password) {
      axios.post('http://localhost:8081/add_product', {
        user_id: sessionStorage.getItem('id'),
        password: password,
        name: this.p.name,
        des: this.p.des_sel,
        adult_price: this.p.adult_price,
        child_price: this.p.child_price,
        days: this.p.days,
        standard: this.r.standard,
        remark: this.r.remark,
        chara: this.r.chara,
        price: this.r.price
      })
      .then(response=> {
        if (response.data === 'ok') {
          this.$message({
            type: 'success',
            message: '提交成功'
          });  
        }
        else {
          if (response.data === 'password') {
            this.$message({
          message: '密码错误',
          type: 'warning'
            });
            return;

          }
          this.$message({
          message: '提交失败',
          type: 'warning'
        });
        }
        
      })

    },
    reset() {
      this.resetForm("ruleForm1");
      this.resetForm("ruleForm2");
    },
    onsubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          //alert("submit!");
          return true;
        } else {
          //alert("error submit!!");
          this.va = false;
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  mounted() {
    this.getDestinations();
  }
};
</script>

<style scoped>
</style>


