<template>
  <div id="employee-table">
    <table>
      <thead>
        <tr>
          <th>出发站</th>
          <th>目的站</th>
          <th>车次</th>
          <th>出发时间</th>
          <th>到达时间</th>
          <th>操作</th>
        </tr>
      </thead>
      <tbody>
        <tr :key="train.id" v-for="train in trains">
          <td v-if="trainEditing === train.id">
            <input style="max-width:70px" type="text" v-model="train.start" />
          </td>
          <td v-else>{{train.start}}</td>
          <td v-if="trainEditing === train.id">
            <input style="max-width:70px" type="text" v-model="train.end" />
          </td>
          <td v-else>{{train.end}}</td>
          <td v-if="trainEditing === train.id">
            <input style="max-width:70px" type="text" v-model="train.number" />
          </td>
          <td v-else>{{train.number}}</td>

          <td v-if="trainEditing === train.id">
            <el-time-picker
            style="width:100px"
              v-model="train.deTime"

              value-format="HH:mm" format="HH:mm"
              :picker-options="{
          selectableRange: '00:00:00 - 23:59:59'
          }"
              placeholder="任意时间点"
            ></el-time-picker>
          </td>
          <td v-else>{{train.deTime}}</td>

          <td v-if="trainEditing === train.id">
            <el-time-picker
            style="width:100px"
              v-model="train.atime"
              value-format="HH:mm" format="HH:mm"
              :picker-options="{
          selectableRange: '00:00:00 - 23:59:59'
          }"
              placeholder="任意时间点"
            ></el-time-picker>
          </td>
          <td v-else>{{train.atime}}</td>

          <td v-if="trainEditing === train.id">
            <button @click="editTrain(train)">保存</button>
            <button class="muted-button" @click="cancelTrainEdit(train)">取消</button>
          </td>
          <td v-else>
            <button @click="editTrainMode(train)">修改</button>
            <button @click="$emit('delete:train', train.id)">删除</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "employee-table",
  props: {
    employees: Array,
    trains: Array
  },
  data() {
    return {
      editing: null,
      trainEditing: null
    };
  },
  methods: {
    editMode(employee) {
      this.cachedEmployee = Object.assign({}, employee);
      this.editing = employee.id;
    },

    editTrainMode(train) {
      this.cachedTrain = Object.assign({}, train);
      this.trainEditing = train.id;
    },
    cancelEdit(employee) {
      Object.assign(employee, this.cachedEmployee);
      this.editing = null;
    },
    cancelTrainEdit(train) {
      Object.assign(train, this.cachedTrain);
      this.trainEditing = null;
    },
    editEmployee(employee) {
      if (employee.name === "" || employee.email === "") return;
      this.$emit("edit:employee", employee.id, employee);
      this.editing = null;
    },
    editTrain(train) {
      if (train.start === '' || train.end === '' ||
      train.number === '' || train.deTime === null ||
      train.atime === null) {
        return
      }
      this.$emit('edit:train', train.id, train);
      this.trainEditing = null
    },
  }
};
</script>

<style scoped>
/*@import url("../assets/css/primitive-ui.css");*/

button {
  margin: 0 0.5rem 0 0;
}

input {
  margin: 0;
  padding: 0%;
}

.empty-table {
  text-align: center;
}


.small-container {
  max-width: 680px;
}
</style>



