<template>
  <div
    id="train"
    class="small-container"
  >
    <h2>火车票</h2>
    <el-divider></el-divider>

    <employee-form @add:employee="addEmployee" 
    @add:train="addTrain"/>
    <employee-table
      :employees="employees"
      @delete:employee="deleteEmployee"
      @edit:employee="editEmployee"
      :trains="trains"
      @edit:train="editTrain"
      @delete:train="deleteTrain"

    />
  </div>
</template>

<script>
import EmployeeTable from '@/components/EmployeeTable.vue'
import EmployeeForm from '@/components/EmployeeForm.vue'

export default {
  name: "train",
  components: {
    EmployeeTable,
    EmployeeForm,
  },
  data() {
    return {
      employees: [],
      trains: [],
    }
  },

  mounted() {
    this.getEmployees()
    this.getTrains()
  },

  methods: {
    async getEmployees() {
      try {
        const response = await fetch('https://jsonplaceholder.typicode.com/users')
        const data = await response.json()
        this.employees = data
      } catch (error) {
        console.error(error)
      }
    },

    async addEmployee(employee) {
      try {
        const response = await fetch('https://jsonplaceholder.typicode.com/users', {
          method: 'POST',
          body: JSON.stringify(employee),
          headers: { "Content-type": "application/json; charset=UTF-8" }
        })
        const data = await response.json()
        this.employees = [...this.employees, data]
      } catch (error) {
        console.error(error)
      }
    },

    async addTrain(train) {
      let send = {
        start: train.start,
        end: train.end,
        number: train.number,
        deTime: train.deTime,
        atime: train.aTime
      }
      try {
        const response = await fetch('http://localhost:8081/add_train', {
          method: 'POST',
          body: JSON.stringify(send),
          headers: { "Content-type": "application/json; charset=UTF-8" }
        })
        const data = await response.json()
        //this.trains = [...this.trains, data]
        this.trains = data
      } catch (error) {
        console.error(error)
      }
    },


    async editEmployee(id, updatedEmployee) {
      try {
        const response = await fetch(`https://jsonplaceholder.typicode.com/users/${id}`, {
          method: 'PUT',
          body: JSON.stringify(updatedEmployee),
          headers: { "Content-type": "application/json; charset=UTF-8" }
        })
        const data = await response.json()
        this.employees = this.employees.map(employee => employee.id === id ? data : employee)
      } catch (error) {
        console.error(error)
      }
    },

    async editTrain(id, updatedTrain) {
      let send = {
        start: updatedTrain.start,
          end: updatedTrain.end,
          number: updatedTrain.number,
          deTime: updatedTrain.deTime,
          atime: updatedTrain.atime
      }
      try {
        const response = await fetch(`http://localhost:8081/trains/${id}`, {
          method: 'POST',
          body: JSON.stringify(send),
          headers: { "Content-type": "application/json; charset=UTF-8" }
        })
        const data = await response.json()
        this.trains = this.trains.map(train => train.id === id ? data : train)
        //this.trains = data
      } catch (error) {
        console.error(error)
      }
    },

    async deleteEmployee(id) {
      try {
        const response = await fetch(`https://jsonplaceholder.typicode.com/users/${id}`, {
          method: 'DELETE'
        })
        const ok = await response.ok;
        if (ok) {
          this.employees = this.employees.filter(employee => employee.id !== id)
        }
      } catch (error) {
        console.error(error)
      }
    },

    async deleteTrain(id) {
      try {
        await fetch(`http://localhost:8081/delete_train/${id}`, {
          method: 'DELETE'
        })
        this.trains = this.trains.filter(train => train.id !== id)
      } catch (error) {
        console.error(error)
      }
    },

    async getTrains() {
      try {
        const response = await fetch('http://localhost:8081/get_trains')
        const data = await response.json()
        this.trains = data
      } catch (error) {
        console.error(error)
      }
    },
  },
}
</script>

<style scoped>

/*@import '../assets/css/primitive-ui.css';*/
/*@import url('https://unpkg.com/primitive-ui/dist/css/main.css');
/*@import url('https://taniarascia.github.io/primitive/css/main.css');*/


button {
  background: #009435;
  border: 1px solid #009435;
}

button:hover,
button:active,
button:focus {
  background: #32a95d;
  border: 1px solid #32a95d;
}

/*.small-container {
  max-width: 680px;
}*/
</style>





