<template>
  <div id="flight-table">
    <el-table :data="tableData" style="width: 100%">
      <el-table-column prop="airline" label="航空公司">
        <template slot-scope="scope">
          <span v-if="scope.$index === editRowIndex">
            <div class="block">
              <el-select v-model="scope.row.airline" placeholder="航空公司">
                <el-option v-for=" i in options" :key="i.id" :label="i.value" :value="i.value"></el-option>
              </el-select>
            </div>
          </span>
          <span v-else>{{scope.row.airline}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="start" label="出发站">
        <template slot-scope="scope">
          <span v-if="scope.$index === editRowIndex">
            <el-input size="small" v-model="scope.row.start"></el-input>
          </span>
          <span v-else>{{scope.row.start}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="end" label="目的站">
        <template slot-scope="scope">
          <span v-if="scope.$index === editRowIndex">
            <el-input size="small" v-model="scope.row.end"></el-input>
          </span>
          <span v-else>{{scope.row.end}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="number" label="航班号">
        <template slot-scope="scope">
          <span v-if="scope.$index === editRowIndex">
            <el-input size="small" v-model="scope.row.number"></el-input>
          </span>
          <span v-else>{{scope.row.number}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="startTime" label="出发时间">
        <template slot-scope="scope">
          <span v-if="scope.$index === editRowIndex">
            <el-time-picker
            style="width:100px"
              v-model="scope.row.startTime"
              value-format="HH:mm"
              format="HH:mm"
              :picker-options="{
          selectableRange: '00:00:00 - 23:59:59'
          }"
              placeholder="任意时间点"
            ></el-time-picker>
          </span>
          <span v-else>{{scope.row.startTime}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="endTime" label="到达时间">
        <template slot-scope="scope">
          <span v-if="scope.$index === editRowIndex">
            <el-time-picker
            style="width:100px"
              v-model="scope.row.endTime"
              value-format="HH:mm"
              format="HH:mm"
              :picker-options="{
          selectableRange: '00:00:00 - 23:59:59'
          }"
              placeholder="任意时间点"
            ></el-time-picker>
          </span>
          <span v-else>{{scope.row.endTime}}</span>
        </template>
      </el-table-column>
      <el-table-column width="250px" label="操作">
        <template slot-scope="scope">
          <el-button
            v-if="scope.$index === editRowIndex"
            size="mini"
            @click="handleSave(scope.$index, scope.row)"
          >保存</el-button>
          <el-button
            v-else
            size="mini"
            type="primary"
            @click="handleEdit(scope.$index, scope.row)"
          >编辑</el-button>
          <el-button
            v-if="scope.$index === editRowIndex"
            size="mini"
            @click="handleCancel(scope.$index, scope.row)"
          >取消</el-button>
          <el-button
            v-else
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "flight-table",
  data() {
    return {
      editRowIndex: null,
      tableData: [],
      options: []
    };
  },
  methods: {
    getFlights() {
      axios.get("http://localhost:8081/get_flights").then(response => {
        this.tableData = response.data;
      });
    },
    handleEdit(index, row) {
      this.editRowIndex = index;
    },
    handleCancel(index, row) {
      this.editRowIndex = null;
    },
    handleSave(index, row) {
      if (row.start === '' || row.end === '' || row.number === '') {
        alert('请填写完整信息')
        this.getFlights()
        this.handleCancel(index, row)
        return
      }
      axios.put("http://localhost:8081/edit_flight", row).then(response => {
        if (response.data === null || response.data === '') {
          this.getFlights();
        }
        else {
          this.tableData = this.tableData.map(employee =>
          employee.id === response.data.id ? response.data : employee
        );
        }
        this.handleCancel(index, row);
      });
    },
    handleDelete(index, row) {
      axios.delete('http://localhost:8081/delete_flight', {data: row})
      .then(response => {
        this.getFlights();
        this.handleCancel(index, row)
      })
    },
    getAirlines() {
      axios.get("http://localhost:8081/get_airlines").then(response => {
        response.data.forEach(e => {
          this.options.push({
            value: e.name,
            label: e.name
          });
        });
      });
    }
  },
  mounted() {
    this.getFlights();
    this.getAirlines();
  }
};
</script>

<style scoped>
</style>


