<template>
  <div id="employee-form">


      <el-form :rules="rules1" :model="train" ref="ruleForm1" :inline="true">
            <el-form-item label="出发站" prop="start">
              <el-input
              @focus="clearStatus();resetForm('ruleForm1');"
                style="width:200px"
                placeholder="请填写出发站"
                v-model="train.start"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item label="目的站" prop="end">
              <el-input
              @focus="clearStatus();resetForm('ruleForm1');"
                style="width:200px"
                placeholder="请填写目的站"
                v-model="train.end"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item label="车次" prop="number">
              <el-input
              @focus="clearStatus();resetForm('ruleForm1');"
                style="width:200px"
                placeholder="请填写车次"
                v-model="train.number"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item label="出发时间" prop="deTime">
            <el-time-picker
            @focus="clearStatus();resetForm('ruleForm1');"
            style="width:200px"
              v-model="train.deTime"
              value-format="HH:mm" format="HH:mm"
              :picker-options="{
          selectableRange: '00:00:00 - 23:59:59'
          }"
              placeholder="任意时间点"
            ></el-time-picker>
          </el-form-item>
          <el-form-item label="到达时间" prop="aTime">
            <el-time-picker
            @focus="clearStatus();resetForm('ruleForm1');"
            style="width:200px"
              v-model="train.aTime"
              value-format="HH:mm" format="HH:mm"
              :picker-options="{
          selectableRange: '00:00:00 - 23:59:59'
          }"
              placeholder="任意时间点"
            ></el-time-picker>
          </el-form-item>
          <br/>
          <p
        v-if="error && submitting"
        class="error-message"
      >❗请填写所有信息</p>
      <p
        v-if="success"
        class="success-message"
      >✅ 火车车次成功添加</p>
          <el-button @click="submit()" type="primary">添加</el-button>
          <br/>
          
      </el-form>
  </div>
</template>

<script>
export default {
  name: 'employee-form',
  data() {
    return {
      error: false,
      submitting: false,
      success: false,
      employee: {
        name: '',
        email: '',
      },

      rules1: {
        start:[{ required: true, message: ' ', trigger: "blur" }],
        end:[{ required: true, message: " ", trigger: "blur" }],
        number:[{ required: true, message: " ", trigger: "blur" }],
        deTime:[{ required: true, message: " ", trigger: "blur" }],
        aTime:[{ required: true, message: " ", trigger: "blur" }],
      },
      train: {
        start: '',
        end: '',
        number: '',
        deTime: '',
        aTime: '',
      }
    }
  },
  computed: {
    invalidName() {
      return this.employee.name === ''
    },

    invalidEmail() {
      return this.employee.email === ''
    },
  },
  methods: {
    handleSubmit() {
      this.clearStatus()
      this.submitting = true

      if (this.invalidName || this.invalidEmail) {
        this.error = true
        return
      }

      this.$emit('add:employee', this.employee)
      this.$refs.first.focus()
      this.employee = {
        name: '',
        email: '',
      }
      this.success = true
      this.error = false
      this.submitting = false
    },

    clearStatus() {
      this.success = false
      this.error = false
      this.$refs['ruleForm1'].clearValidate()
    },
    resetForm(formName) {
      this.$refs['ruleForm1'].errors.clear()
    },
    submit() {
      this.submitting = true;
      this.$refs['ruleForm1'].validate(valid => {
        if (valid) {
          this.$emit('add:train', this.train)
         this.train = {
        start: '',
        end: '',
        number: '',
        deTime: '',
        aTime: '',
      }
      this.success = true
      this.error = false
      this.submitting = false
          return true;
        } else {
          this.error = true
          return false;
        }
      });
    }
  }}
</script>

<style scoped>
form {
  margin-bottom: 2rem;
}

[class*="-message"] {
  font-weight: 500;
}

.error-message {
  color: #d33c40;
}

.success-message {
  color: #32a95d;
}



button {
  background: #009435;
  border: 1px solid #009435;
}

button:hover,
button:active,
button:focus {
  background: #32a95d;
  border: 1px solid #32a95d;
}

.small-container {
  max-width: 680px;
}
</style>



