<template>
  <div id="product-table">
    <h2>我的组团</h2>
    <el-divider></el-divider>
    <div class="group">
      <el-input placeholder="请输入产品名称" icon="search" class="search" v-model="search"></el-input>
    </div>
    <br />
    <el-table :data="tableData" border style="width: 100%">
      <el-table-column  width="120"  prop="productName" label="线路名称"></el-table-column>
      <el-table-column prop="deDate" label="出团日期"></el-table-column>
      <el-table-column prop="reDate" label="返团日期"></el-table-column>
      <el-table-column prop="count" label="人数"></el-table-column>
      <el-table-column label="报名信息">
        <template slot-scope="scope">
          <el-popover placement="bottom" width="400" trigger="click">
            <el-table :data="gridData">
              <el-table-column property="clientName" label="收客门市"></el-table-column>
              <el-table-column property="name" label="费用信息"></el-table-column>
              <el-table-column property="employeeName" label="销售员"></el-table-column>
            </el-table>
            <el-button size="mini" @click="getSalesInfo(scope.row.id)" slot="reference">详 情</el-button>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column prop="plus" label="收入小计"></el-table-column>
      <el-table-column label="地接"></el-table-column>
      <el-table-column label="火车票/机票"></el-table-column>
      <el-table-column label="杂费"></el-table-column>
      <el-table-column label="支出小计"></el-table-column>
      <el-table-column label="毛利"></el-table-column>
      <el-table-column width="230px" label="操作">
        <template slot-scope="scope">
          <el-button size="mini" type="success" @click="handleDelete(scope.$index, scope.row)">报名</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">安排</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "group",
  data() {
    return {
      search: "", //搜索
      tableData: [
      ], //表格内容
      gridData: [
      ],
      salesInfoObject: null,
    };
  },
  computed: {
    tables: function() {
      var search = this.search;
      if (search) {
        return this.tableData.filter(function(dataNews) {
          return Object.keys(dataNews).some(function(key) {
            return (
              String(dataNews[key])
                .toLowerCase()
                .indexOf(search) > -1
            );
          });
        });
      }
      return this.tableData;
    }
  },
  methods: {
    getMyTeams(id) {
      axios.get('http://localhost:8081/get_my_teams/1')
      .then(response => {
        response.data.forEach(element => {
          let count = 0, plus = 0
          element.salesInfo.forEach(e=> {
            count = e.number + count
            plus = e.number * e.unit + plus
          })
          let a = {
            id: element.id,
            productName: element.productName,
            deDate: element.deDate,
            reDate: element.reDate,
            count: count,
            plus: plus,
            salesInfo: element.salesInfo
          }          
          this.tableData.push(a)
        });
      })
    },
    getSalesInfo(id) {
      this.tableData.forEach(e=>{
        if (e.id===id) {
          this.gridData=e.salesInfo
          return
        }
      })
    }
  },
  mounted() {
    //this.getProducts()
    let id = sessionStorage.getItem('id')
    this.getMyTeams(1)
  },
  computed: {
    tableData:function() {

    }
  }
};
</script>

<style scoped>
</style>


