<template>
  <div id="add-product">
    <h2>增加产品</h2>
    <el-divider></el-divider>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="产品名称">
        <div class="block">
          <el-input placeholder="请输入内容" v-model="p.name" clearable></el-input>
        </div>
      </el-form-item>
      <el-form-item label="目的地">
        <el-select v-model="p.des_sel" placeholder="请选择目的地">
          <el-option v-for=" i in des" :key="i.id" :label="i.name" :value="i.id"></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="成年人价格">
        <el-input-number v-model="p.adult_price" :min="0" :max="100000" label></el-input-number>
      </el-form-item>
      <el-form-item label="儿童价格">
        <el-input-number v-model="p.child_price" :min="0" :max="100000" label></el-input-number>
      </el-form-item>
      <el-form-item label="行程天数">
        <el-input-number v-model="p.days" :min="0" :max="100" label></el-input-number>
      </el-form-item>
    </el-form>
    <h3>行程内容</h3>

    <h4>服务标准(此处限填：交通、住宿、用餐、门票、导服、小孩标准、费用不含、购物、保险等)</h4>
    <el-input type="textarea" :rows="8" placeholder="请输入内容" v-model="p.standard"></el-input>

    <h4>行程备注(此处限填：温馨提示、自费项目、自由活动等)</h4>
    <el-input type="textarea" :rows="8" placeholder="请输入内容" v-model="p.remark"></el-input>

    <h4>产品特色(提示：赠送、自由选择)</h4>
    <el-input type="textarea" :rows="8" placeholder="请输入内容" v-model="p.chara"></el-input>

    <h4>产品报价分析(此处限填：设计思路、主要卖点、市场定位、销售渠道、利润产生点、执行周期等)</h4>
    <el-input type="textarea" :rows="8" placeholder="请输入内容" v-model="p.price"></el-input>
    <br />
    <br />
    <div style="text-align: center">
      <el-button type="primary" plain round @click="onsubmit(this.p)">提交产品库</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "AddProduct",
  data() {
    return {
      des: [],
      p: {
        name: "",
        des_sel: "",
        adult_price: "",
        child_price: "",
        days: "",
        standard: "",
        remark: "",
        chara: "",
        price: '',
      }
      
    };
  },
  methods: {
    async getDestinations() {
      try {
        //const response = await fetch('https://jsonplaceholder.typicode.com/users')
        const response = await fetch("http://localhost:8081/destination");
        const data = await response.json();
        this.des = data;
        //this.employees = data
        //alert(JSON.stringify(data));
      } catch (error) {
        alert(error);
      }
    },
    async addProduct(p) {
        try {
            const response = await fetch('http://localhost:8081/add_product', {
            method: 'POST',
            body: JSON.stringify(p),
            headers: { 'Content-type': 'application/json; charset=UTF-8' },
            })
            const data = await response.json()
            //this.employees = [...this.employees, data]
        } catch (error) {
            console.error(error)
        }
    },
    onsubmit() {
        alert("提交");

    }
  },
  mounted() {
    this.getDestinations();
  }
};
</script>

<style scoped>
</style>


