<template>
    <div class="sidebar">
      <el-menu :default-active="$route.path"  class="el-menu-vertical-demo" unique-opened  router>
        <template v-for="bitem in billitem">
          <template v-if="bitem.sublist">
            <el-submenu  :index="bitem.index" v-bind:key="bitem.index">
              <template slot="title"><i  :class="bitem.icon"></i>{{bitem.title}}</template>
              <el-menu-item  v-for="(subitem,myindex) in bitem.sublist" :index="subitem.topath" :key="myindex"  >
                {{subitem.title}}
              </el-menu-item>
            </el-submenu>
          </template>
          <template v-else>
            <el-menu-item  :index="bitem.index" v-bind:key="bitem.index">
              <i  :class="bitem.icon"></i>
              {{bitem.title}}
            </el-menu-item>
          </template>
        </template>

      </el-menu>
    </div>
</template>

<script>
    export default {
        name: 'itemsidebar',
        data: function () {
            return {
              billitem:[
                {
                  icon: 'el-icon-star-on',
                  index: 'sameday',
                  title: '个人中心',
                  sublist: [
                    {
                      topath: '/self_info',
                      title: '我的资料'
                    },
                    {
                      topath: '/notification',
                      title: '通知公告'
                    }
                  ]
                },
                {
                  icon: 'el-icon-upload',
                  index: 'ctyle',
                  title: '业务中心',
                  sublist: [
                    {
                      topath: '/all_products',
                      title: '所有产品',
                    },
                    {
                      topath: '/addproduct',
                      title: '增加产品'
                    },
                    {
                      topath: '/my_groups',
                      title: '我的组团'
                    },
                    {
                      topath: '/addline',
                      title: '增加团队'
                    },
                    {
                      topath: 'add_individuals',
                      title: "报名收客"
                    },
                    {
                      topath: '/local_plans',
                      title: '地接计划（未开放）'
                    }
                  ]
                },
                
                {
                  icon: 'el-icon-menu',
                  index: 'manage',
                  title: '大交通',
                  sublist: [
                    {
                      topath: '/train',
                      title: '火车车次'
                    },
                    {
                      topath: '/flight',
                      title: '航班管理'
                    }
                  ]
                },
                {
                  icon: 'el-icon-menu',
                  index: '/finance',
                  title: '财务中心（未开放）'
                },
                {
                  icon: 'el-icon-date',
                  index: '/administrator',
                  title: '管理员（未开放）'
                },
              ]
            }
        },
        computed: {
          onRoutes: function () {
            console.log(this.$route.path)
            return this.$route.path.replace('/','');  //$route.path表示当前路由，:index能直接跳转路由
          }
        }
    }
</script>

<style scoped>
  .sidebar{
    position:absolute;
    display: block;
    width:200px;
    top:0;
    left:0;
    bottom:0;
  }
  .sidebar > ul {
    height:100%;
  }
</style>


