<template>
    <div id="product-table">
        <h2>所有产品</h2>
    <el-divider></el-divider>
    <div class="search-Box">
      <el-input   placeholder="请输入产品名称" icon="search"  class="search"  v-model="search"></el-input>
    </div>
    <br/>
    <el-table  :data="tables" border style="width: 100%">
      <el-table-column width="200px" prop="name" label="名称"></el-table-column>
      <el-table-column prop="destination" label="目的地"> </el-table-column>
      <el-table-column   prop="days"  label="天数"></el-table-column>
      <el-table-column   prop="employee"  label="发布人"></el-table-column>
      
      <el-table-column width="200px" label="操作">
      <template slot-scope="scope">
        <el-button
          size="mini"
          type="success"
          @click="handleDelete(scope.$index, scope.row)">行程</el-button>
          <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">查看团期</el-button>
      </template>
    </el-table-column>
    </el-table>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'product-table',
    data(){
      return {
        search: '',  //搜索
        tableData: [
        ]  //表格内容
      }
    },
    computed:{
      tables:function(){
        var search=this.search;
        if(search){
          return  this.tableData.filter(function(dataNews){
            return Object.keys(dataNews).some(function(key){
              return String(dataNews[key]).toLowerCase().indexOf(search) > -1
            })
          })
        }
        return this.tableData
      }
    },
    methods: {
        getProducts() {
            axios.get('http://localhost:8081/get_products')
            .then(response=> {
                this.tableData = response.data
            })

        }
    },
    mounted() {
        this.getProducts()
    }
}
</script>

<style scoped>

</style>


