<template>
  <div class="header">
    <div class="billlogo">时代知旅（重庆）国际旅行社</div>
    <div class="userimgbox">
      <el-dropdown>
        <span class="el-dropdown-link">
          <img src="../../assets/logo.png" class="userimg" />
        </span>

        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>
            <div>{{name1}}</div>
          </el-dropdown-item>
          <el-dropdown-item @click.native="exit1()">安全退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "itemheader",
  data: function() {
    return {
      name1: "sdsd"
    };
  },
  methods: {
    errorHandle() {
      return true;
    },
    async getName(id) {
      try {
        let user = {
          "id": id
        }
        const response = await fetch(
          "http://localhost:8081/name",
          {
            method: "POST",
            body: JSON.stringify(user),
            headers: { "Content-type": "application/json; charset=UTF-8" }
          }
        );
        const data = await response.json();
        this.name1 = data.name;
      } catch (error) {
        console.error(error);
      }
    },
    exit1() {
      sessionStorage.setItem('id', 0);
      sessionStorage.setItem('token', '');
      sessionStorage.setItem('role', 0);
      this.$router.push('/login')
    
    }
  },
  mounted() {
    this.getName(sessionStorage.getItem('id'));
  }
};
</script>

<style scoped>
.header {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  line-height: 60px;
  color: white;
  background-color: #409eff;
}
.billlogo {
  text-align: left;
  margin-left: 20px;
  float: left;
}
.userimgbox {
  float: right;
  height: 60px;
}
.userimg {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  margin-right: 40px;
  vertical-align: middle;
}
</style>



