<template>
  <div id="flight-form">
    <el-form :rules="rules1" :model="flight" ref="ruleForm1" :inline="true">
      <el-form-item label="航空公司" prop="airline">
        <div class="block">
          <el-select v-model="flight.airline" placeholder="航空公司">
                <el-option v-for=" i in options" :key="i.id" :label="i.value" :value="i.value"></el-option>
              </el-select>
        </div>
      </el-form-item>
      <el-form-item label="出发站" prop="start">
        <el-input
          @focus="clearStatus();resetForm('ruleForm1');"
          style="width:200px"
          placeholder="请填写出发站"
          v-model="flight.start"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="目的站" prop="end">
        <el-input
          @focus="clearStatus();resetForm('ruleForm1');"
          style="width:200px"
          placeholder="请填写目的站"
          v-model="flight.end"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="航班" prop="number">
        <el-input
          @focus="clearStatus();resetForm('ruleForm1');"
          style="width:200px"
          placeholder="请填写航班"
          v-model="flight.number"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="出发时间" prop="startTime">
        <el-time-picker
          @focus="clearStatus();resetForm('ruleForm1');"
          style="width:200px"
          v-model="flight.startTime"
          value-format="HH:mm"
          format="HH:mm"
          :picker-options="{
          selectableRange: '00:00:00 - 23:59:59'
          }"
          placeholder="任意时间点"
        ></el-time-picker>
      </el-form-item>
      <el-form-item label="到达时间" prop="endTime">
        <el-time-picker
          @focus="clearStatus();resetForm('ruleForm1');"
          style="width:200px"
          v-model="flight.endTime"
          value-format="HH:mm"
          format="HH:mm"
          :picker-options="{
          selectableRange: '00:00:00 - 23:59:59'
          }"
          placeholder="任意时间点"
        ></el-time-picker>
      </el-form-item>
      <br />
      <p v-if="error && submitting" class="error-message">❗请填写所有信息</p>
      <p v-if="success" class="success-message">✅ 成功添加航班</p>
      <el-button @click="submit()" type="primary">添加</el-button>

      <br />
    </el-form>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "flight-form",
  data() {
    return {
      error: false,
      submitting: false,
      success: false,
      flight: {
        airline: "",
        start: "",
        end: "",
        number: "",
        startTime: "",
        endTime: ""
      },
      options: [],

      rules1: {
        airline: [{ required: true, message: " ", trigger: "blur" }],
        start: [{ required: true, message: " ", trigger: "blur" }],
        end: [{ required: true, message: " ", trigger: "blur" }],
        number: [{ required: true, message: " ", trigger: "blur" }],
        startTime: [{ required: true, message: " ", trigger: "blur" }],
        endTime: [{ required: true, message: " ", trigger: "blur" }]
      }
    };
  },
  computed: {
    invalidName() {
      return this.employee.name === "";
    },

    invalidEmail() {
      return this.employee.email === "";
    }
  },
  methods: {
    handleSubmit() {
      this.clearStatus();
      this.submitting = true;

      if (this.invalidName || this.invalidEmail) {
        this.error = true;
        return;
      }

      this.$emit("add:employee", this.employee);
      this.$refs.first.focus();
      this.employee = {
        name: "",
        email: ""
      };
      this.success = true;
      this.error = false;
      this.submitting = false;
    },

    clearStatus() {
      this.success = false;
      this.error = false;
    },
    resetForm(formName) {
      this.$refs["ruleForm1"].clearValidate();
    },
    submit() {
      this.submitting = true;
      this.$refs["ruleForm1"].validate(valid => {
        if (valid) {
          axios
            .post("http://localhost:8081/add_flight", this.flight)
            .then(response => {
              if (response.status === 200) {
                  this.$parent.getFlights()
                
                this.flight = {
                  airline: "",
                  start: "",
                  end: "",
                  number: "",
                  startTime: "",
                  endTime: ""
                };
                this.success = true;
                this.error = false;
                this.submitting = false;
                return true;
              } else {
                alert("失败");
                return false;
              }
            });
        } else {
          this.error = true;
          return false;
        }
      });
    },
    getAirlines() {
      axios.get("http://localhost:8081/get_airlines").then(response => {
        response.data.forEach(e => {
          this.options.push({
            value: e.name,
            label: e.name
          });
        });
      });
    }
  },
  mounted() {
    this.getAirlines();
  }
};
</script>

<style scoped>
</style>


